/**********************/
/*  CAMBIOS GENERALES   */
/**********************/

:root {
    --c1: #D8D5CE;
    --c2: #9AA36E;
    --c3: #B49F8A;
    --c4: #A8A7A2;
    --c5: #F0EFED;
    --c-dorado: #BF7E04;
    --c1lighter: #fbfbfa;
    --font-principal: 3rem;
    --font-normal: 2.2rem;
}

* {
    margin: 0;
    padding: 0;
}

html {
    font-size: 62.5%;
}

body {
    font-size: var(--font-normal);
    max-width: 100vw;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: auto;
    text-align: center;
    color: #51563A;
    font-family: 'Pridi', serif;
}

p {
    font-size: var(--font-normal);
}

/**********************/
/*    LAYOUT    */
/**********************/

.box {
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    max-width: 1440px;
    padding: 5rem;
    margin: auto;
}

a.button--secondary,
a.button--primary {
    padding: 10px 50px;
    border-radius: 8px 0;
    font-family: 'Pridi', serif;;
}

header {
    height: 10rem;
    background-color: var(--c5);

    position: fixed;
    top: 0;
    width: 100%;
    z-index: 9999;
}

.header__box {

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__nav-bar {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap-reverse;

    list-style-type: none;
    font-size: 2.5rem;
    flex-grow: 9;
}

.header__nav-bar li a {
    padding: 5px;
    margin: 0 5px;
    color: var(--c1);
    border-radius: 8px 8px 0px 0px;
}

/*---------------------*/

.flex-izq {
    /* background-color: tomato; */
    width: 50%;
    display: flex;
}

.flex-der {
    /* background-color: brown; */
    width: 50%;
    display: flex;
}

section {
    min-height: 90vh;
    /* Para navegadores que no soporten calc */
    min-height: calc(100vh - 10rem);
}

.sec1 {
    margin-top: 10rem;
    background-color: var(--c1lighter);
    display: flex;
    /* align-content: center; */
}

.sec1__box {
    display: flex;
    /* background-color: tomato; */
}

.sec1__flex-izq {
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.sec1__box h1 {
    margin: 0 0 50px 0;
}

.sec1__box p {
    /* background-color: red; */
    margin: 0 0 80px 0;
    width: 80%;
    font-size: 3rem;
    text-align: center;
    line-height: 4rem;
}

.sec1__video {
    width: 90%;
    height: 80%;
    margin: auto;
}
/*---------------------*/
.sec2 {
    background-color: var(--c1);
    display: flex;
    flex-direction: column;
}

.sec2__box {
    display: flex;
    padding-top: 0;
    /* background-color: tomato; */
}

.sec2__flex-izq .img1 {
    width: 250px;
    height: 300px;
    position: relative;
    bottom: 20px;
    left: 20px;
    opacity: 0.8;
    filter: grayscale(0.8);
}

.sec2__flex-izq .img2{
    width: 250px;
    height: 300px;
    position: relative;
    top: 20px;
    right: 20px;
    opacity: 0.9;
    filter: saturate(0.8);
}

.sec2__flex-izq {
    /* background-color: brown; */
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    flex-grow: 5;
}

.sec2__flex-der {
    /* background-color: tomato; */
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 50px 0px;
    flex-grow: 2;
    display: flex;
}

.sec2__flex-der p {
    width: 75%;
    font-size: var(--font-principal);
    margin: 20px 0 0 0;
    text-align: center;
    line-height: 3.5rem;
}

.sec2__button {
    margin-top: 50px;
}

.button--secondary{
    background-color: var(--c1lighter);
    border: solid 3px var(--c-dorado);
}

.button--secondary {
    color:  var(--c-dorado);
}

/*---------------------*/

.sec3__box {
    display: flex;
    /* flex-direction: column; */
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 60%;
    padding: 0 0;
}

.sec3__img-izq {
    float: left;
}

.sec3__img-der {
    float: right;
}

.sec3__box img {
    height: 300px;
    width: 260px;
    margin: 50px;
}

.sec3__box p {
    text-align: center;
    font-size: var(--font-principal);
}

.sec3__button {
    display:block;
    margin: auto;
    margin-top: 50px;
    width: 280px;
}
/**********************/
.sec4 {
    background-color: var(--c1);
}

.sec4__box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
}

.sec4__collage {
    width: 90%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    /* background-color: tomato; */
}

.sec4__collage img {
    display: block;
    margin: auto;
    padding: 20px;

}
/**********************/

.sec5 {
    background-color: var(--c5);
}

.sec5__box {
    padding-top: 0;
    padding-bottom: 0;
}

.sec5__info {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.box__info {
    width: 250px;
    /* background-color: tomato; */
    min-height: 180px;
    padding: 20px;
    margin: 50px;
    border: solid 3px var(--c-dorado);
    border-radius: 10px 0;
    display: flex;
    flex-direction: column;
}

.box__info p {
    margin-top: 10px;
    text-align: center;
    margin: auto;
}

.box__info h4 {
    justify-self: flex-start;
} 

.sec5__button {
    display: block;
    margin: auto;
    text-align: center;
    width: 340px;
}

.button--primary {
    background-color: var(--c1lighter);
    border: solid 3px var(--c-dorado);
}

.button--primary {
    color:  var(--c-dorado);
}
/**********************/

footer {
    background-color: var(--c2);
}

.footer__box {
    padding: 50px 50px;
    /* background-color: brown; */
    width: 90%;
    margin: auto;
    display: flex;

}

.part1 {
    /* background-color: red; */
    flex-grow: 4;
}

.part2 {
    /* background-color: yellow; */
    flex-grow: 2;
}

div.part1 span,
div.part2 span {
    display: block;
    padding: 5px;
    color: #513B24;
}

/*******************************/
/*  TRANSICIONES Y ANIMACIONES */
/*******************************/

.nav-bar li a {
    transition: all 0.3s;
}

a.button--secondary,
a.button--primary {
    transition: all 0.3s;
}

.container_animation {
    /* background: url(./img/elca-logo.png); */
    background-color: tomato;
    width: 252px;
    height: 300px;
    position: relative;
    perspective: 400px;
    .cubo {
        transform-style: preserve-3d;
    }
    .cubo,
    .cubo div {
        width: 100%;
        height: 100%;
        position: absolute;
        margin: 0;
    }
}
.cubo_lado1{}
.cubo_lado2{
    background:hsla(91, 100%, 50%, 0.5);
    transform: rotateY(120deg);  
    .text_lado2 {
        display: flex;  
        justify-content: center;
        align-items: center;
        width: 50px;
        height: 50px;
        background-color: #fff;
    }
}

/***********************/
/*  TAMAÑO DE IMÁGENES */
/***********************/

.box_logo {
    width: 250px;
    height: 80px;
    /* background-color: red; */
    margin: 0 120px;

    display: flex;
    align-items: center;
}

.logo {
    max-width: 100%;
    height: 100%;
    margin: auto;
}

.tabla-servicios tr td img {
    width: 200px;
    height: 180px;
}

.s3 article img {
    width: 300px;
    height: 250px;
}



