/* NOSOTROS */

.flex-column {
    display: flex;
    flex-direction: column;
}

.sec1__nosotros {
    flex-direction: column;
    margin: 7rem;
}

.iframe_nosotros {
    display: block;
    margin: auto;
}

/* SERVICIOS */

.art_tabla-servicios {
    padding: 10px 0;
    
}

.tabla-servicios tr {
    text-align: center;
    background-color: var(--c1lighter);
}

.tabla-servicios tr td {
    padding: 10px;
    margin: 0;
    vertical-align: top;
    border-bottom: 2px solid black;
}

.tabla-servicios tr td ul {
    list-style-type: square;
    width: 250px;
    margin: 20px 50px;
    text-align: left;
    vertical-align: center;
}


/* PROYECTOS */

.art_proyectos {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}

.art_proyectos img {
    width: 300px;
    height: 250px;
    padding: 25px;
    margin: auto;
}

/* CONTACTO */

.form {
    margin: 20px 0;
}

